import React, { useState } from 'react';

const PersonalInfoSection = ({ 
  fullName, 
  email, 
  phone, 
  location, 
  onSavePersonalInfo 
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [formData, setFormData] = useState({
    fullName: fullName,
    email: email,
    phone: phone,
    location: location
  });

  const togglePersonalInfoEdit = () => {
    setIsEditing(!isEditing);
    if (!isEditing) {
      setFormData({
        fullName: fullName,
        email: email,
        phone: phone,
        location: location
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSave = () => {
    onSavePersonalInfo(formData);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setFormData({
      fullName: fullName,
      email: email,
      phone: phone,
      location: location
    });
    setIsEditing(false);
  };

  // Israeli cities list for personal info
  const israeliCities = [
    'Tel Aviv-Yafo',
    'Jerusalem',
    'Haifa',
    'Rishon LeZion',
    'Petah Tikva',
    'Ashdod',
    'Netanya',
    'Beer Sheva',
    'Holon',
    'Ramat Gan',
    'Ashkelon',
    'Rehovot',
    'Herzliya',
    'Kfar Saba',
    'Bat Yam',
    'Ramat HaSharon',
    'Lod',
    'Ness Ziona',
    'Eilat',
    'Nahariya',
    'Acre',
    'Tiberias',
    'Nazareth',
    'Umm al-Fahm',
    'Rahat',
    'Sderot',
    'Dimona',
    'Kiryat Gat',
    'Kiryat Shmona',
    'Ma\'alot-Tarshiha',
    'Ofakim',
    'Arad',
    'Karmiel',
    'Tayibe',
    'Kiryat Bialik',
    'Kiryat Motzkin',
    'Kiryat Yam',
    'Kiryat Ata',
    'Kiryat Tivon',
    'Yokneam Illit',
    'Migdal HaEmek',
    'Nesher',
    'Tirat Carmel',
    'Yokneam',
    'Ma\'ale Adumim',
    'Ma\'alot',
    'Kiryat Malakhi',
    'Kiryat Ono',
    'Givatayim',
    'Rosh HaAyin',
    'Modi\'in',
    'Yavne',
    'Or Yehuda',
    'Kfar Yona',
    'Tira',
    'Kafr Qasim',
    'Qalansawe',
    'Ar\'ara',
    'Kafr Manda',
    'Kafr Kanna',
    'Iksal',
    'Baqa al-Gharbiyye',
    'Jisr az-Zarqa',
    'Fureidis',
    'Mazkeret Batya',
    'Gedera',
    'Ra\'anana',
    'Hod HaSharon',
    'Even Yehuda',
    'Tzur Yigal',
    'Kadima-Zoran',
    'Tel Mond',
    'Kfar Hess',
    'Beit Dagan',
    'Gan Yavne',
    'Kfar Menahem',
    'Kfar Bilu',
    'Kfar Malal'
  ];

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">Personal Information</h3>
        <button className="edit-btn" onClick={togglePersonalInfoEdit}>
          <i className="fas fa-edit"></i> Edit
        </button>
      </div>
      {!isEditing ? (
        <div id="personalInfoDisplay">
          <div className="info-item">
            <div className="info-label">Full Name:</div>
            <div className="info-value" id="fullNameValue">{fullName || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Email:</div>
            <div className="info-value" id="emailValue">{email || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Phone:</div>
            <div className="info-value" id="phoneValue">{phone || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Location:</div>
            <div className="info-value" id="locationValue">{location || 'Not specified'}</div>
          </div>
        </div>
      ) : (
        <div id="personalInfoEdit">
          <div className="form-group">
            <label className="form-label">Full Name:</label>
            <input 
              type="text" 
              className="form-input" 
              id="fullNameInput" 
              name="fullName"
              value={formData.fullName}
              onChange={handleInputChange}
              placeholder="Enter full name"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Email:</label>
            <input 
              type="email" 
              className="form-input" 
              id="emailInput" 
              name="email"
              value={formData.email}
              onChange={handleInputChange}
              placeholder="Enter email"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Phone:</label>
            <input 
              type="tel" 
              className="form-input" 
              id="phoneInput" 
              name="phone"
              value={formData.phone}
              onChange={handleInputChange}
              placeholder="Enter phone number"
            />
          </div>
          <div className="form-group">
            <label className="form-label">Location:</label>
            <select 
              className="form-input" 
              id="locationInput" 
              name="location"
              value={formData.location}
              onChange={handleInputChange}
            >
              <option value="">Select City</option>
              {israeliCities.map(city => (
                <option key={city} value={city}>{city}</option>
              ))}
            </select>
          </div>
          <div className="form-actions">
            <button className="btn btn-primary" onClick={handleSave}>Save</button>
            <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default PersonalInfoSection;
