import React, { useState } from 'react';

const CandidateTicketSection = ({ 
  ticketData, 
  onSaveTicket
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [selectedField, setSelectedField] = useState('');
  const [selectedJobType, setSelectedJobType] = useState('');
  const [selectedCity, setSelectedCity] = useState('');
  const [formData, setFormData] = useState({
    city: ticketData.city ? (typeof ticketData.city === 'string' ? ticketData.city.split(',').map(c => c.trim()).filter(c => c) : ticketData.city) : [],
    experience: ticketData.experience,
    remoteWork: ticketData.remoteWork,
    jobType: ticketData.jobType ? (typeof ticketData.jobType === 'string' ? ticketData.jobType.split(',').map(j => j.trim()).filter(j => j) : ticketData.jobType) : [],
    field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
    degree: ticketData.degree
  });

  const toggleTicketEdit = () => {
    setIsEditing(!isEditing);
    if (!isEditing) {
      setFormData({
        city: ticketData.city ? (typeof ticketData.city === 'string' ? ticketData.city.split(',').map(c => c.trim()).filter(c => c) : ticketData.city) : [],
        experience: ticketData.experience,
        remoteWork: ticketData.remoteWork,
        jobType: ticketData.jobType ? (typeof ticketData.jobType === 'string' ? ticketData.jobType.split(',').map(j => j.trim()).filter(j => j) : ticketData.jobType) : [],
        field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
        degree: ticketData.degree
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddField = () => {
    if (selectedField && !formData.field.includes(selectedField)) {
      setFormData(prev => ({
        ...prev,
        field: [...prev.field, selectedField]
      }));
      setSelectedField('');
    }
  };

  const handleRemoveField = (index) => {
    setFormData(prev => ({
      ...prev,
      field: prev.field.filter((_, i) => i !== index)
    }));
  };

  const handleAddJobType = () => {
    if (selectedJobType && !formData.jobType.includes(selectedJobType)) {
      setFormData(prev => ({
        ...prev,
        jobType: [...prev.jobType, selectedJobType]
      }));
      setSelectedJobType('');
    }
  };

  const handleRemoveJobType = (index) => {
    setFormData(prev => ({
      ...prev,
      jobType: prev.jobType.filter((_, i) => i !== index)
    }));
  };

  const handleAddCity = () => {
    if (selectedCity && !formData.city.includes(selectedCity)) {
      setFormData(prev => ({
        ...prev,
        city: [...prev.city, selectedCity]
      }));
      setSelectedCity('');
    }
  };

  const handleRemoveCity = (index) => {
    setFormData(prev => ({
      ...prev,
      city: prev.city.filter((_, i) => i !== index)
    }));
  };

  const handleSave = async () => {
    console.log('CandidateTicketSection handleSave called with formData:', formData);
    // Convert field, jobType, and city arrays to comma-separated strings for backend
    const dataToSave = {
      ...formData,
      field: formData.field.join(', '),
      jobType: formData.jobType.join(', '),
      city: formData.city.join(', ')
    };
    await onSaveTicket(dataToSave);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setFormData({
      city: ticketData.city ? (typeof ticketData.city === 'string' ? ticketData.city.split(',').map(c => c.trim()).filter(c => c) : ticketData.city) : [],
      experience: ticketData.experience,
      remoteWork: ticketData.remoteWork,
      jobType: ticketData.jobType ? (typeof ticketData.jobType === 'string' ? ticketData.jobType.split(',').map(j => j.trim()).filter(j => j) : ticketData.jobType) : [],
      field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
      degree: ticketData.degree
    });
    setIsEditing(false);
  };

  // Israeli cities list for candidate ticket (multiple selection)
  const israeliCities = [
    'Tel Aviv-Yafo',
    'Jerusalem',
    'Haifa',
    'Rishon LeZion',
    'Petah Tikva',
    'Ashdod',
    'Netanya',
    'Beer Sheva',
    'Holon',
    'Ramat Gan',
    'Ashkelon',
    'Rehovot',
    'Herzliya',
    'Kfar Saba',
    'Bat Yam',
    'Ramat HaSharon',
    'Lod',
    'Ness Ziona',
    'Eilat',
    'Nahariya',
    'Acre',
    'Tiberias',
    'Nazareth',
    'Umm al-Fahm',
    'Rahat',
    'Sderot',
    'Dimona',
    'Kiryat Gat',
    'Kiryat Shmona',
    'Ma\'alot-Tarshiha',
    'Ofakim',
    'Arad',
    'Karmiel',
    'Tayibe',
    'Kiryat Bialik',
    'Kiryat Motzkin',
    'Kiryat Yam',
    'Kiryat Ata',
    'Kiryat Tivon',
    'Yokneam Illit',
    'Migdal HaEmek',
    'Nesher',
    'Tirat Carmel',
    'Yokneam',
    'Ma\'ale Adumim',
    'Ma\'alot',
    'Kiryat Malakhi',
    'Kiryat Ono',
    'Givatayim',
    'Rosh HaAyin',
    'Modi\'in',
    'Yavne',
    'Or Yehuda',
    'Kfar Yona',
    'Tira',
    'Kafr Qasim',
    'Qalansawe',
    'Ar\'ara',
    'Kafr Manda',
    'Kafr Kanna',
    'Iksal',
    'Baqa al-Gharbiyye',
    'Jisr az-Zarqa',
    'Fureidis',
    'Mazkeret Batya',
    'Gedera',
    'Ra\'anana',
    'Hod HaSharon',
    'Even Yehuda',
    'Tzur Yigal',
    'Kadima-Zoran',
    'Tel Mond',
    'Kfar Hess',
    'Beit Dagan',
    'Gan Yavne',
    'Kfar Menahem',
    'Kfar Bilu',
    'Kfar Malal'
  ];


  const remoteOptions = ['Remote', 'On-Site', 'Hybrid'];

  const jobTypes = [
    'Full-time', 'Part-time', 'Contract', 'Internship', 'Freelance'
  ];

  const fields = [
    'Software Development', 'Data Science', 'Cybersecurity', 'DevOps', 'UI/UX Design',
    'Product Management', 'Marketing', 'Sales', 'Finance', 'Human Resources',
    'Operations', 'Customer Success', 'Business Development', 'Content Creation',
    'Graphic Design', 'Digital Marketing', 'Project Management', 'Quality Assurance',
    'System Administration', 'Network Engineering', 'Mobile Development', 'Web Development',
    'Game Development', 'Artificial Intelligence', 'Machine Learning', 'Cloud Computing',
    'Database Administration', 'Technical Writing', 'Research & Development', 'Consulting'
  ];

  const degreeOptions = [
    'No Degree',
    'Bachelor\'s Degree',
    'Master\'s Degree',
    'PhD or Doctorate',
    'College Diploma',
    'Professional Certification',
    'Bootcamp Graduate',
    'High School Diploma'
  ];

  return (
    <div className="section-card candidate-ticket-section">
      <div className="section-header">
        <h3 className="section-title">Candidate Ticket</h3>
        <button 
          className="edit-btn" 
          onClick={toggleTicketEdit} 
          aria-label="Edit ticket information" 
          aria-expanded={isEditing} 
          aria-controls="ticketEditArea"
        >
          <i className="fas fa-edit" aria-hidden="true"></i> Edit
        </button>
      </div>
      {!isEditing ? (
        <div id="ticketDisplay">
          <div className="info-item">
            <div className="info-label">Cities:</div>
            <div className="info-value" id="ticketCityValue">
              {ticketData.city ? (
                typeof ticketData.city === 'string' ? (
                  ticketData.city.split(',').map(c => c.trim()).filter(c => c).length > 0 ? (
                    <ul className="city-list">
                      {ticketData.city.split(',').map(c => c.trim()).filter(c => c).map((city, index) => (
                        <li key={index}>{city}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                ) : (
                  ticketData.city.length > 0 ? (
                    <ul className="city-list">
                      {ticketData.city.map((city, index) => (
                        <li key={index}>{city}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                )
              ) : 'Not specified'}
            </div>
          </div>
          <div className="info-item">
            <div className="info-label">Experience:</div>
            <div className="info-value" id="ticketExperienceValue">{ticketData.experience || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Remote Work:</div>
            <div className="info-value" id="ticketRemoteValue">{ticketData.remoteWork || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Job Type:</div>
            <div className="info-value" id="ticketJobTypeValue">
              {ticketData.jobType ? (
                typeof ticketData.jobType === 'string' ? (
                  ticketData.jobType.split(',').map(j => j.trim()).filter(j => j).length > 0 ? (
                    <ul className="job-type-list">
                      {ticketData.jobType.split(',').map(j => j.trim()).filter(j => j).map((jobType, index) => (
                        <li key={index}>{jobType}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                ) : (
                  ticketData.jobType.length > 0 ? (
                    <ul className="job-type-list">
                      {ticketData.jobType.map((jobType, index) => (
                        <li key={index}>{jobType}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                )
              ) : 'Not specified'}
            </div>
          </div>
          <div className="info-item">
            <div className="info-label">Field:</div>
            <div className="info-value" id="ticketFieldValue">
              {ticketData.field ? (
                typeof ticketData.field === 'string' ? (
                  ticketData.field.split(',').map(f => f.trim()).filter(f => f).length > 0 ? (
                    <ul className="field-list">
                      {ticketData.field.split(',').map(f => f.trim()).filter(f => f).map((field, index) => (
                        <li key={index}>{field}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                ) : (
                  ticketData.field.length > 0 ? (
                    <ul className="field-list">
                      {ticketData.field.map((field, index) => (
                        <li key={index}>{field}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                )
              ) : 'Not specified'}
            </div>
          </div>
          <div className="info-item">
            <div className="info-label">Degree:</div>
            <div className="info-value" id="ticketDegreeValue">{ticketData.degree || 'Not specified'}</div>
          </div>
        </div>
      ) : (
        <div id="ticketEdit">
          <div className="form-group">
            <label className="form-label">Cities:</label>
            <div className="multi-select-container">
              <div className="selected-items">
                {formData.city.map((city, index) => (
                  <span key={index} className="selected-item">
                    {city}
                    <button 
                      type="button" 
                      className="remove-btn" 
                      onClick={() => handleRemoveCity(index)}
                      aria-label={`Remove ${city}`}
                    >
                      
                    </button>
                  </span>
                ))}
              </div>
              <div className="add-item-controls">
                <select 
                  className="form-input" 
                  id="ticketCityInput" 
                  value={selectedCity}
                  onChange={(e) => setSelectedCity(e.target.value)}
                >
                  <option value="">Select City</option>
                  {israeliCities.map(city => (
                    <option key={city} value={city}>{city}</option>
                  ))}
                </select>
                <button 
                  type="button" 
                  className="add-btn" 
                  onClick={handleAddCity}
                  disabled={!selectedCity}
                >
                  Add City
                </button>
              </div>
            </div>
          </div>
          <div className="form-group">
            <label className="form-label">Experience (Years):</label>
            <input 
              type="number" 
              className="form-input" 
              id="ticketExperienceInput" 
              name="experience"
              value={formData.experience}
              onChange={handleInputChange}
              min="0"
              max="100"
              required
            />
          </div>
          <div className="form-group">
            <label className="form-label">Remote Work:</label>
            <select 
              className="form-input" 
              id="ticketRemoteInput" 
              name="remoteWork"
              value={formData.remoteWork}
              onChange={handleInputChange}
            >
              <option value="">Select Remote Option</option>
              {remoteOptions.map(option => (
                <option key={option} value={option}>{option}</option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-label">Job Type:</label>
            <div className="job-type-input">
              <select
                className="form-input"
                value={selectedJobType}
                onChange={(e) => setSelectedJobType(e.target.value)}
              >
                <option value="">Select a job type</option>
                {jobTypes.map((jobType, index) => (
                  <option key={index} value={jobType}>
                    {jobType}
                  </option>
                ))}
              </select>
              <button
                type="button"
                className="btn btn-secondary btn-sm"
                onClick={handleAddJobType}
                disabled={!selectedJobType}
              >
                <i className="fas fa-plus"></i> Add
              </button>
            </div>
            
            {formData.jobType && formData.jobType.length > 0 && (
              <div className="job-type-list">
                <h4>Selected Job Types:</h4>
                <ul className="job-type-tags">
                  {formData.jobType.map((jobType, index) => (
                    <li key={index} className="job-type-tag">
                      <span>{jobType}</span>
                      <button
                        type="button"
                        className="remove-job-type"
                        onClick={() => handleRemoveJobType(index)}
                      >
                        <i className="fas fa-times"></i>
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
          <div className="form-group">
            <label className="form-label">Field:</label>
            <div className="field-input">
              <select
                className="form-input"
                value={selectedField}
                onChange={(e) => setSelectedField(e.target.value)}
              >
                <option value="">Select a field</option>
                {fields.map((field, index) => (
                  <option key={index} value={field}>
                    {field}
                  </option>
                ))}
              </select>
              <button
                type="button"
                className="btn btn-secondary btn-sm"
                onClick={handleAddField}
                disabled={!selectedField}
              >
                <i className="fas fa-plus"></i> Add
              </button>
            </div>
            
            {formData.field && formData.field.length > 0 && (
              <div className="field-list">
                <h4>Selected Fields:</h4>
                <ul className="field-tags">
                  {formData.field.map((field, index) => (
                    <li key={index} className="field-tag">
                      <span>{field}</span>
                      <button
                        type="button"
                        className="remove-field"
                        onClick={() => handleRemoveField(index)}
                      >
                        <i className="fas fa-times"></i>
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
          <div className="form-group">
            <label className="form-label">Degree:</label>
            <select 
              className="form-input" 
              id="ticketDegreeInput" 
              name="degree"
              value={formData.degree}
              onChange={handleInputChange}
            >
              <option value="">Select Degree</option>
              {degreeOptions.map(degree => (
                <option key={degree} value={degree}>{degree}</option>
              ))}
            </select>
          </div>
          <div className="form-actions">
            <button className="btn btn-primary" onClick={handleSave}>Save</button>
            <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default CandidateTicketSection;
